"bre": { "id": "bre", "name": "Flughafen Bremen", "nameEnglish": "Bremen Airport", "city": "Bremen", "country": "Germany", "description": "Opened in 1920 and originally called Neuenlander Feld, *Bre*men Airport serves as a starting point for commercial pilot careers at Lufthansa.", "imageCredit": "Peronimo", "imageCreditLink": "https://www.flickr.com/photos/peronimo/" },
"bro": { "id": "bro", "name": "Brownsville/South Padre Island International Airport", "city": "Brownsville", "city2": "South Padre Island", "state": "Texas", "stateShort": "TX", "country": "USA", "description": "Formerly Brownsville Army Airfield, Brownsville/South Padre Island International gets its three-letter code from its home in *Bro*wnsville, Texas.", "imageCredit": "Fly Brownsville", "imageCreditLink": "http://www.flybrownsville.com" },
"brq": { "id": "brq", "name": "Letit BrnoTuany", "nameEnglish": "BrnoTuany Airport", "city": "Brno", "city2": "Tuany", "country": "Czech Republic", "description": "Built in the 1950s, BrnoTuany Airport gets its airport code from the city of *Br*no, plus a helpful *Q* as a filler letter.", "imageCredit": "Eugene Tarasov", "imageCreditLink": "https://www.flickr.com/photos/eugeneti/" },
"brr": { "id": "brr", "name": "Port-adhair Bharraigh", "nameEnglish": "Barra Airport", "city": "Barra", "city2": "Outer Hebrides", "state": "Scotland", "country": "UK", "description": "Opened in 1936, Barra Airport is located at the north tip of the island of *B*a*rr*a in the Outer Hebrides, Scotland. It is the only airport in the world using a beach as its runway.", "imageCredit": "Bocian & Tusia", "imageCreditLink": "https://www.flickr.com/photos/arturbocian/" },
"brs": { "id": "brs", "name": "Bristol Airport", "city": "Bristol", "city2": "Lulsgate Bottom", "state": "England", "country": "UK", "description": "Used as RAF Lulsgate Bottom during World War II, Bristol Airport is located at Lulsgate Bottom in North Somerset and serves the city of *Br*i*s*tol.", "imageCredit": "Andy Munzer", "imageCreditLink": "https://www.flickr.com/photos/munzer/" },
