"cta": { "id": "cta", "name": "Aeroporto di Catania-Fontanarossa", "nameEnglish": "CataniaFontanarossa Airport", "city": "Catania", "country": "Italy", "description": "Also known as Vincenzo Bellini Airport after the famous opera composer, *C*a*t*ani*a*Fontanarossa Airport was the regions first airport, built in 1924.", "imageCredit": "fabricata", "imageCreditLink": "https://www.flickr.com/photos/fattycata/" },
