"gdn": { "id": "gdn", "name": "Port Lotniczy Gdask im. Lecha Wasy", "nameEnglish": "Gdansk Lech Walesa Airport", "city": "Gdask", "country": "Poland", "description": "Opened in 1974, Gdansk Lech Walesa Airport is named after Lech Walesa, former Polish president. Its airport code honors its home city of *Gd*a*n*sk.", "imageCredit": "magro_kr", "imageCreditLink": "https://www.flickr.com/photos/iks_berto/" },
