"gig": { "id": "gig", "name": "Aeroporto Internacional do GaleoAntonio Carlos Jobim", "nameEnglish": "GaleoAntonio Carlos Jobim International Airport", "city": "Rio de Janeiro", "state": "Rio de Janeiro", "stateShort": "RJ", "country": "Brazil", "description": "Formerly known as Galeo Air Force Base, Galeo International was created in 1941 on *G*overnador *I*slands *G*aleo Beach in Rio de Janeiro.", "imageCredit": "Victor Camilo", "imageCreditLink": "https://www.flickr.com/photos/victorcamilo/" },
