"gjt": { "id": "gjt", "name": "Grand Junction Regional Airport", "city": "Grand Junction", "state": "Colorado", "stateShort": "CO", "country": "USA", "description": "*G*rand *J*unc*t*ion Regional is also called Walker Field, named after Walter Walker, a local publisher who helped raise funding and support for the airports development.", "imageCredit": "Marshall Segal", "imageCreditLink": "https://www.flickr.com/photos/marshallsegal/" },
"gla": { "id": "gla", "name": "Port-adhair Eadar-niseanta Ghlaschu", "nameEnglish": "Glasgow Airport", "city": "Glasgow", "state": "Scotland", "country": "UK", "description": "Known as HMS Sanderling during World War II and later as Glasgow Abbotsinch Airport, Scotlands second busiest airport honors its home in *Gla*sgow.", "imageCredit": "Paisley Scotland", "imageCreditLink": "https://www.flickr.com/photos/paisleyorguk/" },
