"gmu": { "id": "gmu", "name": "Greenville Downtown Airport", "city": "Greenville", "state": "South Carolina", "stateShort": "SC", "country": "USA", "description": "Originally built in 1928 it was known as *G*reenville *Mu*nicipal Airport, and was the origination of Lynyrd Skynyrds fatal flight in 1977.", "imageCredit": "Chssam", "imageCreditLink": "https://commons.wikimedia.org/wiki/File:Aerial_view_of_the_Greenville_Downtown_Airport.jpg" },
"gmz": { "id": "gmz", "name": "Aeropuerto de La Gomera", "nameEnglish": "La Gomera Airport", "city": "La Gomera", "city2": "Playa Santiago", "state": "Canary Islands", "country": "Spain", "description": "La Gomera airport is located near the town of Playa Santiago on the island of La *G*o*m*era. The Z in its code could come from the province of Santa Cru*z* de Tenerife, but is likely a filler letter.", "imageCredit": "Javier Toledo", "imageCreditLink": "https://www.flickr.com/photos/jtoledo/" },
