"gpt": { "id": "gpt", "name": "GulfportBiloxi International Airport", "city": "GulfportBiloxi", "city2": "Biloxi", "state": "Mississippi", "stateShort": "MS", "country": "USA", "description": "Built in 1942 and named Gulfport Army Airfield, GulfportBiloxi International gets its three-letter code from its home in the city of *G*ulf*p*or*t*.", "imageCredit": "Fly GPT", "imageCreditLink": "http://www.flygpt.com" },
