"gyd": { "id": "gyd", "name": "Heydar Aliyev International Airport", "city": "Baku", "country": "Azerbaijan", "description": "In 2004, Bakus airport was renamed and changed its airport code from BAK to GYD in honor of former Azerbaijan President Heydar Aliyev, sometimes spelled *G*a*yd*ar Aliev.", "imageCredit": "ge'shmally", "imageCreditLink": "https://www.flickr.com/photos/geshmally/" },
