"ham": { "id": "ham", "name": "Flughafen Hamburg", "nameEnglish": "Hamburg Airport", "city": "Hamburg", "country": "Germany", "description": "Hamburg Airport was opened in January of 1911 and serves Germanys second-largest city, *Ham*burg.", "imageCredit": "Medvedev", "imageCreditLink": "http://commons.wikimedia.org/wiki/File:HH-Airport_Terminal2_03.jpg" },
"han": { "id": "han", "name": "Ni Bi International Airport", "name2": "Noi Bai International Airport", "city": "Hanoi", "country": "Vietnam", "description": "Opened in 1978, Noi Bai International gets its three-letter airport code from its home in the city of *Han*oi.", "imageCredit": "Simon Morris", "imageCreditLink": "https://www.flickr.com/photos/simon_morris/" },
