"hft": { "id": "hft", "name": "Hammerfest lufthavn", "nameEnglish": "Hammerfest Airport", "city": "Hammerfest", "country": "Norway", "description": "Opened in 1974, Hammerfest Airport is Norways third-busiest regional airport and serves *H*ammer*f*es*t*.", "imageCredit": "Bair175", "imageCreditLink": "https://commons.wikimedia.org/wiki/File:Hammerfest_lufthavn.jpg" },
"hga": { "id": "hga", "name": "Hargeisa Egal International Airport", "city": "Hargeisa", "country": "Somalia", "description": "Hargeisa Egal International is named after Muhammad Haji Ibrahim Egal, former Prime Minister of Somalia. Its airport code comes from its home in the city of *H*ar*g*eis*a*.", "imageCredit": "YoTuT", "imageCreditLink": "https://www.flickr.com/photos/yotut/" },
