"hgl": { "id": "hgl", "name": "Flughafen Helgoland-Dne", "nameEnglish": "Heligoland Airport", "city": "Heligoland", "country": "Germany", "description": "The *H*eli*g*o*l*and Airport is situated on the small island Dne near the main island Heligoland. It serves mainly small tourist flights towards Bremerhaven and Bsum.", "imageCredit": "Louis-F. Stahl", "imageCreditLink": "https://commons.wikimedia.org/wiki/File:EDXH_Flugplatz_Helgoland.JPG" },
