"hkd": { "id": "hkd", "name": "Hakodate Airport", "city": "Hakodate", "city2": "Hokkaido", "country": "Japan", "description": "Opened in 1961, Hakodate Airport gets its airport code from its home in the city of *H*a*k*o*d*ate.", "imageCredit": "Hideyuki KAMON", "imageCreditLink": "https://www.flickr.com/photos/hyougushi/" },
"hkg": { "id": "hkg", "name": "Hong Kong International Airport", "city": "Hong Kong", "city2": "Chek Lap Kok", "country": "China", "description": "Hong Kong International is located on the island of Chek Lap Kok and is often called Chek Lap Kok Airport. Its three-letter airport code stands for *H*ong *K*on*g*.", "imageCredit": "Chris", "imageCreditLink": "https://www.flickr.com/photos/dcmaster/" },
"hkt": { "id": "hkt", "name": "Phuket International Airport", "city": "Phuket", "country": "Thailand", "description": "Phuket International is an important gateway to Thailand for tourists visiting P*h*u*k*e*t* Island. It is Thailands third busiest airport.", "imageCredit": "Andy Mitchell", "imageCreditLink": "https://www.flickr.com/photos/monstermunch/" },
"hlp": { "id": "hlp", "name": "Bandar Udara Halim Perdanakusuma", "nameEnglish": "Halim Perdanakusuma International Airport", "city": "Jakarta", "city2": "East Jakarta", "country": "Indonesia", "description": "Halim Perdanakusuma International is Jakartas primary airport for military, private, and government flights. It is named after Air Vice-Marshal and national hero Abdul *H*a*l*im *P*erdanakusuma for his service in World War II.", "imageCredit": "Andri Priyo Utomo", "imageCreditLink": "https://www.flickr.com/photos/icokz/" },
