"hnm": { "id": "hnm", "name": "Hana Airport", "city": "Hana", "state": "Hawaii", "stateShort": "HI", "country": "USA", "description": "Hana Airport is a single runway located near the town of *H*a*n*a, on the east shore of *M*aui.", "imageCredit": "Forest and Kim Starr", "imageCreditLink": "https://www.flickr.com/photos/starr-environmental/" },
