"hov": { "id": "hov", "name": "rstaVolda lufthavn, Hovden", "nameEnglish": "rstaVolda Airport, Hovdene", "city": "rsta", "city2": "Volda", "city3": "Hovdebygda", "country": "Norway", "description": "rstaVolda Airport is located between the cities of rsta and Volda, but gets its three-letter code from the nearby villge of *Hov*den.", "imageCredit": "Egil Arne", "imageCreditLink": "http://commons.wikimedia.org/wiki/File:%C3%98rsta_Volda_lufthamn_Hovden.jpg" },
"hph": { "id": "hph", "name": "Sn bay Quc t Ct Bi", "nameEnglish": "Cat Bi International Airport", "city": "Hai Phong", "country": "Vietnam", "description": "Undergoing rennovations in 2015, Cat Bi International gets its airport code from its home in *H*ai *Ph*ong.", "imageCredit": "David McKelvey", "imageCreditLink": "https://www.flickr.com/photos/dgmckelvey/" },
