"hre": { "id": "hre", "name": "Harare International Airport", "city": "Harare", "country": "Zimbabwe", "description": "Opened in 1957 as Salisbury Airport, Harare International serves the city of *H*ara*re* in Zimbabwe.", "imageCredit": "Annette Lyn O'Neil", "imageCreditLink": "https://www.flickr.com/photos/annette_oneil/" },
"hrg": { "id": "hrg", "name": "Hurghada International Airport", "city": "Hurghada", "country": "Egypt", "description": "Hurghada International is the second busiest airport in Egypt and gets its three-letter code from its home in the city of *H*u*rg*hada.", "imageCredit": "Henning Leweke", "imageCreditLink": "https://www.flickr.com/photos/59683764@N00/" },
"hri": { "id": "hri", "name": "Mattala Rajapaksa International Airport", "city": "Hambantota", "city2": "Mattala", "country": "Sri Lanka", "description": "Mattala Rajapaksa International is named after the Rajapaksas, a prominent political family. Its airport codes comes from its location near *H*ambantota, S*ri* Lanka.", "imageCredit": "Anuradha Dullewe Wijeyeratne", "imageCreditLink": "http://commons.wikimedia.org/wiki/File:Mattala_Rajapaksa_International_Airport_Check-in_counters.jpg" },
