"icn": { "id": "icn", "name": "Incheon International Airport", "city": "Seoul", "city2": "Jung District", "state": "Korea", "country": "South Korea", "description": "The airport that serves the Seoul Metro Area is actually located on a man-made piece of land between Yeongjong and Yongyu islands, west of *I*n*c*heo*n*s city center.", "imageCredit": "Marcella Bona", "imageCreditLink": "https://www.flickr.com/photos/marcella_bona/" },
"ict": { "id": "ict", "name": "Dwight D. Eisenhower National Airport", "city": "Wichita", "state": "Kansas", "stateShort": "KS", "country": "USA", "description": "The FCC restricted U.S. airport codes from beginning with the letters W and K, reserving them for radio station designations. Formerly named Mid-Continent Airport, W*ic*hi*t*a opted for ICT.", "imageCredit": "Michael Sauers", "imageCreditLink": "https://www.flickr.com/photos/travelinlibrarian/" },
