"ika": { "id": "ika", "name": "Tehran Imam Khomeini International Airport", "city": "Tehran", "city2": "Ahmadabad", "country": "Iran", "description": "Located in Ahmadabad and serving the city of Tehran, *I*mam *K*homeini *A*irport is named after Ruhollah Khomeini, leader of the 1979 Iranian Revolution.", "imageCredit": "Mamooli", "imageCreditLink": "https://www.flickr.com/photos/babairan/" },
