"inu": { "id": "inu", "name": "Nauru International Airport", "city": "Yaren", "country": "Nauru", "description": "Built during World War II under Japanese occupation, Nauru International is the only airport on the *I*sland of *N*aur*u*.", "imageCredit": "DFAT Australia", "imageCreditLink": "https://www.flickr.com/photos/dfataustralianaid/" },
