"isb": { "id": "isb", "name": "Benazir Bhutto International Airport", "city": "Islamabad", "city2": "Rawalpindi", "state": "Punjab", "stateShort": "PB", "country": "Pakistan", "description": "Renamed in 2008, Benazir Bhutto International honors the former prime minister of Pakistan. Its airport code honors its home in the twin cities of Rawalpindi and *Is*lama*b*ad.", "imageCredit": "Muzaffar Bukhari", "imageCreditLink": "https://www.flickr.com/photos/mbukhari/" },
"isc": { "id": "isc", "name": "St Marys Airport", "city": "Isles of Scilly", "city2": "Cornwall", "state": "England", "country": "UK", "description": "Previously High Cross Farm and officially opened in 1939, St Marys Airport is located in St Marys in the *I*sles of *Sc*illy near Cornwall.", "imageCredit": "calflier001", "imageCreditLink": "https://www.flickr.com/photos/calflier001/" },
"isp": { "id": "isp", "name": "Long Island MacArthur Airport", "city": "Long Island", "city2": "Islip", "state": "New York", "stateShort": "NY", "country": "USA", "description": "Long Island Airport got its start in the 1940s and is named after U.S. Army General Douglas MacArthur. Its airport code comes from its original name of *Is*li*p* Airport.", "imageCredit": "Mr.TinDC", "imageCreditLink": "https://www.flickr.com/photos/mr_t_in_dc/" },
"ist": { "id": "ist", "name": "stanbul Atatrk Havaliman", "nameEnglish": "Istanbul Atatrk Airport", "city": "Istanbul", "city2": "Yeilky", "country": "Turkey", "description": "Turkeys busiest airport honors the first president of the Republic of Turkey, Mustafa Kemal Atatrk. Its airport code honors the city of *Ist*anbul.", "imageCredit": "jaime.silva", "imageCreditLink": "https://www.flickr.com/photos/20792787@N00/" },
"itm": { "id": "itm", "name": "Osaka International Airport", "city": "Osaka", "city2": "Itami", "country": "Japan", "description": "Formerly Itami Airport, Osaka International was renamed following its return to Japanese control in 1959. While it serves Osaka, it is actually located in the city of *It*a*m*i.", "imageCredit": "Joe Jones", "imageCreditLink": "https://www.flickr.com/photos/redjoe/" },
"ito": { "id": "ito", "name": "Hilo International Airport", "city": "Hilo", "state": "Hawaii", "stateShort": "HI", "country": "USA", "description": "One theory about ITO says it was named after an early station manager, Mr. Ito. Though with HIL and ILO already taken, Hilo International likely took its three-letter code from the nickname H*i*lo *To*wn.", "imageCredit": "Frances", "imageCreditLink": "https://www.flickr.com/photos/50163579@N00/" },
"iwa": { "id": "iwa", "name": "Ivanovo Yuzhny Airport", "city": "Ivanovo", "country": "Russia", "description": "Ivanovo Yuzhny Airport gets its three-letter airport code from its home in the city of *I*v*a*novo. Unable to use the V, it swapped in a helpful filler *W*.", "imageCredit": "Ramn", "imageCreditLink": "https://www.flickr.com/photos/sitomon/" },
"ixa": { "id": "ixa", "name": "Agartala Airport", "city": "Agartala", "state": "Tripura", "stateShort": "TR", "country": "India", "description": "Agartala Airport is named after the capital of Tripura state in India. Its airport code comes from the new code series for India issued by IATA, *IX*, plus the city of *A*gartala.", "imageCredit": "PP Yoonus", "imageCreditLink": "http://commons.wikimedia.org/wiki/File:Agartala_Airport_Departure_Terminal.jpeg" },
"ixb": { "id": "ixb", "name": "Bagdogra Airport", "city": "Bagdogra", "city2": "Siliguri", "state": "West Bengal", "stateShort": "WB", "country": "India", "description": "Located in the town of Siliguri, Bagdogra Airport serves the Darjeeling district of West Bengal. Its airport code comes from the new code series for India issued by IATA, *IX*, plus the city of *B*agdogra.", "imageCredit": "Abymac", "imageCreditLink": "http://commons.wikimedia.org/wiki/File:Bagdogra_International_Airport.jpg" },
"ixc": { "id": "ixc", "name": "Chandigarh International Airport", "city": "Chandigarh", "stateShort": "CH", "country": "India", "description": "Chandigarh International is named after the union territory in northern India, also known as The City Beautiful. Its airport code comes from the new code series for India issued by IATA, *IX*, plus the city of *C*handigarh.", "imageCredit": "Aries Liang", "imageCreditLink": "http://en.wikipedia.org/wiki/File:Chandigarh_Airport_-_mural.jpg" },
"ixm": { "id": "ixm", "name": "Madurai Airport", "city": "Madurai", "state": "Tamil Nadu", "stateShort": "TN", "country": "India", "description": "Madurai Airport was established in 1957, after serving as a military airfield. Its airport code comes from the new code series for India issued by IATA, *IX*, plus the city of *M*adurai.", "imageCredit": "SarThePhotographer", "imageCreditLink": "http://commons.wikimedia.org/wiki/File:MaduraiAirport.JPG" },
"ixz": { "id": "ixz", "name": "Veer Savarkar International Airport", "city": "Port Blair", "state": "Andaman and Nicobar Islands", "stateShort": "AN", "country": "India", "description": "Veer Savarkar International is named after the Indian freedom activist Vinayak Damodar Savarkar. Its airport code comes from the new code series for India issued by IATA, *IX*, with the letter *Z* as filler.", "imageCredit": "Jpatokal", "imageCreditLink": "http://commons.wikimedia.org/wiki/File:PortBlairAirport.JPG" },
"jac": { "id": "jac", "name": "Jackson Hole Airport", "city": "Jackson", "city2": "Teton County", "state": "Wyoming", "stateShort": "WY", "country": "USA", "description": "Wyomings busiest airport, the only commercial airport located inside a U.S. national park, gets its airport code from the nearby town of *Jac*kson.", "imageCredit": "Sarah Nichols", "imageCreditLink": "https://www.flickr.com/photos/pocheco/" },
"jai": { "id": "jai", "name": "Jaipur International Airport", "city": "Jaipur", "state": "Rajasthan", "stateShort": "RJ", "country": "India", "description": "Jaipur International is the only international airport in the state of Rajasthan. Its airport code honors its home in the city of *Jai*pur.", "imageCredit": "Sranjanm2002", "imageCreditLink": "http://commons.wikimedia.org/wiki/File:JaipurGate3.jpg" },
"jau": { "id": "jau", "name": "Aeropuerto Francisco Carl", "nameEnglish": "Francisco Carl Airport", "city": "Jauja", "state": "Junn", "country": "Peru", "description": "Francisco Carl Airport serves the city of *Jau*ja and its surrounding cities. Its airport code honors its home city.", "imageCredit": "AgainErick", "imageCreditLink": "https://commons.wikimedia.org/wiki/File:Airport_Jauja_Peru.jpg" },
