"jed": { "id": "jed", "name": "King Abdulaziz International Airport", "city": "Jeddah", "country": "Saudi Arabia", "description": "King Abdulaziz International is named after the first monarch of Saudi Arabia, and because of its proximity to Makkah, is best known for its spacious Hajj terminal. Its airport code honors the city of *Jed*dah.", "imageCredit": "IFC Infrastructure", "imageCreditLink": "https://www.flickr.com/photos/ifcinfrastructure/" },
