"jib": { "id": "jib", "name": "Aroport international de DjiboutiAmbouli", "nameEnglish": "DjiboutiAmbouli International Airport", "city": "Djibouti City", "city2": "Ambouli", "country": "Djibouti", "description": "Opened in 1948, DjiboutiAmbouli International is located in the town of Ambouli and serves D*jib*outi City.", "imageCredit": "Ryan Kilpatrick", "imageCreditLink": "https://www.flickr.com/photos/rkilpatrick21/" },
"jnb": { "id": "jnb", "name": "O. R. Tambo International Airport", "city": "Johannesburg", "city2": "Kempton Park", "country": "South Africa", "description": "Previously *J*oha*n*nes*b*urg International, O. R. Tambo International was renamed in 2006 to honor Oliver Tambo, former African National Congress President.", "imageCredit": "Len Edgerly", "imageCreditLink": "https://www.flickr.com/photos/lenedgerly/" },
"jog": { "id": "jog", "name": "Bandar Udara Internasional Adisucipto", "nameEnglish": "Adisucipto International Airport", "city": "Yogyakarta", "city2": "Central Java", "country": "Indonesia", "description": "Adisucipto International serves the Yogyakarta (previously anglicized *Jog*jakarta) metro area, and honors Agustinus Adisucipto, national hero and early figure in the Indonesian Air Force.", "imageCredit": "Debbs.", "imageCreditLink": "https://www.flickr.com/photos/anemptyhouse/" },
"joi": { "id": "joi", "name": "Aeroporto de Joinville-Lauro Carneiro de Loyola", "nameEnglish": "Joinville-Lauro Carneiro de Loyola Airport", "city": "Joinville", "state": "Santa Catarina", "stateShort": "SC", "country": "Brazil", "description": "Lauro Carneiro de Loyola Airport is named after Lauro Falco Carneiro, local entrepreneur and politician. Its three-letter code comes from the city of *Joi*nville.", "imageCredit": "Matheus Wahl", "imageCreditLink": "http://commons.wikimedia.org/wiki/File:Aeroporto_de_Joinville_1.JPG" },
