"jsi": { "id": "jsi", "name": "Skiathos Airport Alexandros Papadiamantis", "city": "Skiathos", "state": "Thessaly", "country": "Greece", "description": "Skiathos Airport is popular for its close take-offs and landings due to its short runway, similar to SXM. The *SI* in its code comes from *S*k*i*athos, while the *J* is likely a filler letter. JSI is one of nine Greek airports whose code begins with J.", "imageCredit": "Aero Icarus", "imageCreditLink": "https://www.flickr.com/photos/aero_icarus/" },
