"ker": { "id": "ker", "name": "Kerman International Airport", "city": "Kerman", "country": "Iran", "description": "Kerman International gets its airport code from its home in the city and province of *Ker*man.", "imageCredit": "A.Davey", "imageCreditLink": "https://www.flickr.com/photos/adavey/" },
