"krk": { "id": "krk", "name": "Krakw Airport im. Jana Pawa II", "nameEnglish": "John Paul II International Airport", "city": "Krakw", "city2": "Balice", "country": "Poland", "description": "Opened in 1964, KrakwBalice Airport was renamed in 1995 to honor Pope John Paul II who before the Papacy had served as Archbishop for the city of *Kr*a*k*w.", "imageCredit": "Alquiler de Coches", "imageCreditLink": "https://www.flickr.com/photos/reservasdecoches/" },
