"ksa": { "id": "ksa", "name": "Kosrae International Airport", "city": "Kosrae", "country": "Micronesia", "description": "Kosrae International is located on an artificial island in eastern Micronesia. It serves the state of *K*o*s*r*a*e.", "imageCredit": "Mike LaMonaca", "imageCreditLink": "https://www.flickr.com/photos/philamike/" },
