"oua": { "id": "oua", "name": "Ouagadougou International Airport", "city": "Ouagadougou", "country": "Burkina Faso", "description": "Built in the 1960s, Ouagadougou International gets its three-letter airport code from the capital of Burkina Faso, *Oua*gadougou.", "imageCredit": "SEWA", "imageCreditLink": "https://www.flickr.com/photos/solar_energy/" },
"oul": { "id": "oul", "name": "Oulun lentoasema", "nameEnglish": "Oulu Airport", "city": "Oulu", "country": "Finland", "description": "Oulu Airport is Finlands second busiest airport and its airport code reflects its home in the city of *Oul*u.", "imageCredit": "Sami Oinonen", "imageCreditLink": "https://www.flickr.com/photos/sami_oinonen/" },
"ovb": { "id": "ovb", "name": "Novosibirsk Tolmachevo Airport", "city": "Novosibirsk", "country": "Russia", "description": "Opened in 1957, Novosibirsk Tolmachevo Airport gets its three-letter code from the city of N*ov*osi*b*irsk.", "imageCredit": "Obakeneko", "imageCreditLink": "http://commons.wikimedia.org/wiki/File:Domestic_terminal_of_Tolmachevo_Airport_at_night.jpg" },
