"pap": { "id": "pap", "name": "Aroport International Toussaint L'Ouverture", "nameEnglish": "Toussaint L'Ouverture International Airport", "city": "Port-au-Prince", "city2": "Tabarre", "country": "Haiti", "description": "Started as an airfield in the 1940s, Toussaint L'Ouverture Airport has had many names, but is currently named after the leader of the Haitian Revolution. Located in the town of Tabarre, it serves the capital city of *P*ort-*a*u-*P*rince.", "imageCredit": "Antonio Zugaldia", "imageCreditLink": "https://www.flickr.com/photos/azugaldia/" },
"pbg": { "id": "pbg", "name": "Plattsburgh International Airport", "city": "Plattsburgh", "state": "New York", "stateShort": "NY", "country": "USA", "description": "Located at the former Plattsburgh Air Force Base, Plattsburgh International took over traffice from Clinton County Airport in 2007. Its airport code comes from the city of *P*latts*b*ur*g*h.", "imageCredit": "joshparent", "imageCreditLink": "https://www.flickr.com/photos/joshparent/" },
"pbh": { "id": "pbh", "name": "Paro International Airport", "city": "Paro", "country": "Bhutan", "description": "Originally built in 1968 to serve the Indian Armed Forces, Paro Airport is the only international airport in the country. Its airport code comes from the district of *P*aro, *Bh*utan.", "imageCredit": "Gran Hglund (Kartlsarn)", "imageCreditLink": "https://www.flickr.com/photos/kartlasarn/" },
"pbi": { "id": "pbi", "name": "Palm Beach International Airport", "city": "Palm Beach", "city2": "Miami", "state": "Florida", "stateShort": "FL", "country": "USA", "description": "Originally opened in 1936 and named Morrison Field, *P*alm *B*each *I*nternational was renamed in 1948, with a stint as Palm Beach Air Force Base from 1951-1962.", "imageCredit": "D Ramey Logan", "imageCreditLink": "http://commons.wikimedia.org/wiki/File:Palm_Beach_International_Airport_photo_D_Ramey_Logan.jpg" },
