"pdg": { "id": "pdg", "name": "Bandar Udara Internasional Minangkabau", "nameEnglish": "Minangkabau International Airport ", "city": "Padang", "state": "West Sumatra", "country": "Indonesia", "description": "Minangkabau International is named after the Minangkabau, the indigenous people of West Sumatra. Its airport code honors its home in the city of *P*a*d*an*g*.", "imageCredit": "Dekoelie", "imageCreditLink": "https://commons.wikimedia.org/wiki/File:Minangkabau_Airport.JPG" },
"pdl": { "id": "pdl", "name": "Aeroporto Joo Paulo II", "nameEnglish": "Joo Paulo II Airport", "city": "Ponta Delgada", "city2": "So Miguel", "stateShort": "Azores", "country": "Portugal", "description": "Inaugurated in 1969, Joo Paulo II Airport was renamed in 1995 to honor the 1991 visit of Pope John Paul II to the Azores. Located on the island of So Miguel, its airport code honors the city of *P*onta *D*e*l*gada.", "imageCredit": "Aero Icarus", "imageCreditLink": "https://www.flickr.com/photos/aero_icarus/" },
"pdv": { "id": "pdv", "name": "Letishte Plovdiv", "nameEnglish": "Plovdiv Airport", "city": "Plovdiv", "city2": "Krumovo", "country": "Bulgaria", "description": "Often called Krumovo Airport, *P*lov*d*i*v* Airport primarily serves visitors to the ski resort towns of Bansko and Pamporovo.", "imageCredit": "Christian Rasmussen", "imageCreditLink": "https://www.flickr.com/photos/apoltix/" },
"pdx": { "id": "pdx", "name": "Portland International Airport", "city": "Portland", "state": "Oregon", "stateShort": "OR", "country": "USA", "description": "Before the 1930s, airports had two-letter codes. When codes switched to three letters, many added the letter X to the end. PD (*P*ortlan*d*) became PDX.", "imageCredit": "Kerry L", "imageCreditLink": "https://www.flickr.com/photos/kerrypolka/" },
