"sba": { "id": "sba", "name": "Santa Barbara Municipal Airport", "city": "Santa Barbara", "state": "California", "stateShort": "CA", "country": "USA", "description": "Built on a former cow pasture, Santa Barbara Municipal gets its airport code from its home in the *S*anta *Ba*rbara/Goleta metropolitan area.", "imageCredit": "Adam Fagen", "imageCreditLink": "https://www.flickr.com/photos/afagen/" },
"sbd": { "id": "sbd", "name": "San Bernardino International Airport", "city": "San Bernardino", "state": "California", "stateShort": "CA", "country": "USA", "description": "San Bernardino International is located on the former site of Norton Air Force Base and serves the city of *S*an *B*ernar*d*ino.", "imageCredit": "Ron Kroetz", "imageCreditLink": "https://www.flickr.com/photos/ronkroetz/" },
"sbn": { "id": "sbn", "name": "South Bend International Airport", "city": "South Bend", "state": "Indiana", "stateShort": "IN", "country": "USA", "description": "Opened in 1933 as Bendix Field, South Bend International was once called Michiana Regional (a combination of Michigan and Indiana). Its airport code honors its home in *S*outh *B*e*n*d.", "imageCredit": "Jo & Joey", "imageCreditLink": "https://www.flickr.com/photos/eguchi_onion/" },
