"scl": { "id": "scl", "name": "Aeropuerto Internacional Comodoro Arturo Merino Bentez", "nameEnglish": "Comodoro Arturo Merino Bentez International Airport", "city": "Santiago", "city2": "Pudahuel", "country": "Chile", "description": "Often called Pudahuel Airport, Comodoro Arturo Merino Bentez International honors the founder of the Chilean Air Force. Its airport code honors its home in *S*antiago, *C*hi*l*e.", "imageCredit": "lane becker", "imageCreditLink": "https://www.flickr.com/photos/monstro/" },
