"sdf": { "id": "sdf", "name": "Louisville International Airport", "city": "Louisville", "state": "Kentucky", "stateShort": "KY", "country": "USA", "description": "Before being renamed in 1995, Louisville International was known as *S*tan*d*i*f*ord Field, honoring former state senator Elisha David Standiford.", "imageCredit": "Lisa Padilla", "imageCreditLink": "https://www.flickr.com/photos/lisap/" },
