"sti": { "id": "sti", "name": "Aeropuerto Internacional del Cibao", "nameEnglish": "Cibao International Airport", "city": "Santiago de los Caballeros", "country": "Dominican Republic", "description": "Inaugurated in 2002, Cibao International serves the Cibao region of Dominican Republic. Its airport code comes from its home city of *S*an*ti*ago de los Caballeros.", "imageCredit": "Lacreta", "imageCreditLink": "http://commons.wikimedia.org/wiki/File:COA-B772enSTI2007.JPG" },
"stl": { "id": "stl", "name": "LambertSt. Louis International Airport", "city": "St. Louis", "state": "Missouri", "stateShort": "MO", "country": "USA", "description": "Lambert-St. Louis International is named after Albert Bond Lambert, Olympic golfer and the first to receive a pilot's license in the city of *St*. *L*ouis.", "imageCredit": "Rich", "imageCreditLink": "https://www.flickr.com/photos/richmanwisco/" },
"stn": { "id": "stn", "name": "London Stansted Airport", "city": "London", "city2": "Stansted Mountfitchet", "state": "England", "country": "UK", "description": "Started as an airfield during World War II, London *St*a*n*sted Airport opened its first commercial terminal building in 1969 and is a base for many major European low-cost carriers.", "imageCredit": "John Lord", "imageCreditLink": "https://www.flickr.com/photos/yellowbookltd/" },
"stp": { "id": "stp", "name": "St. Paul Downtown Airport", "city": "Saint Paul", "city2": "Twin Cities", "state": "Minnesota", "stateShort": "MN", "country": "USA", "description": "Built in 1927, Holman Field was named after airline and stunt pilot Charles W. Holman. Today, the airport is known as the *St*. *P*aul Downtown Airport.", "imageCredit": "Tony Webster", "imageCreditLink": "https://www.flickr.com/photos/diversey/" },
"str": { "id": "str", "name": "Flughafen Stuttgart - Manfred Rommel Flughafen", "nameEnglish": "Stuttgart Airport", "city": "Stuttgart", "country": "Germany", "description": "Stuttgart Airport was renamed in 2014 to honor former mayor Manfred Rommel. It is the sixth busiest airport in Germany and serves the city of *St*uttga*r*t.", "imageCredit": "Dieter Weinelt", "imageCreditLink": "https://www.flickr.com/photos/weinelt/" },
