"stt": { "id": "stt", "name": "Cyril E. King Airport", "city": "Saint Thomas", "city2": "St. Thomas", "stateShort": "Virgin Islands", "country": "USA", "description": "Previously named after Harry S. Truman, Cyril E. King Airport was renamed in 1984 to honor the former governor of the U.S. Virgin Islands. Its airport code comes from its home on the island of *St*. *T*homas.", "imageCredit": "Akshat Aranya", "imageCreditLink": "https://www.flickr.com/photos/aaranya/" },
"stx": { "id": "stx", "name": "Henry E. Rohlsen Airport", "city": "Saint Croix", "city2": "St. Croix", "stateShort": "Virgin Islands", "country": "USA", "description": "Henry E. Rohlsen Airport is named after Henry E. Rohlsen, a Tuskegee Airman during World War II. Its three-letter code honors its home on the island of *St*. Croi*x*.", "imageCredit": "Lars Schmidt", "imageCreditLink": "http://commons.wikimedia.org/wiki/File:Henry_E._Rohlsen_Airport_(Tower).jpg" },
"sub": { "id": "sub", "name": "Bandar Udara Internasional Juanda", "nameEnglish": "Juanda International Airport", "city": "Surabaya", "city2": "Sidoarjo", "state": "East Java", "country": "Indonesia", "description": "Juanda International is the second-busiest airport in Indonesia. It is named after the last Indonesian Prime Minister, Djuanda Kartawidjaja, but gets its airport code from the city it serves: *Su*ra*b*aya.", "imageCredit": "123_456", "imageCreditLink": "https://www.flickr.com/photos/123_456/" },
