"sux": { "id": "sux", "name": "Sioux Gateway Airport", "city": "Sioux City", "state": "Iowa", "stateShort": "IA", "country": "USA", "description": "*S*io*ux* City petitioned twice to have its airport code SUX changed. With no great alternatives, they stuck with it and now use the slogan Fly SUX.", "imageCredit": "David Mulder", "imageCreditLink": "https://www.flickr.com/photos/113026679@N03/" },
