"svg": { "id": "svg", "name": "Stavanger lufthavn, Sola", "nameEnglish": "Stavanger Airport, Sola", "city": "Stavanger", "city2": "Sola", "country": "Norway", "description": "Opened in 1937, Stavanger Airport is the second oldest in Norway. Located in Sola, it gets its airport code from the nearby city of *S*ta*v*an*g*er.", "imageCredit": "karlrpet", "imageCreditLink": "https://www.flickr.com/photos/54973802@N00/" },
"svo": { "id": "svo", "name": "Sheremetyevo International Airport", "city": "Moscow", "city2": "Khimki", "country": "Russia", "description": "Opened in 1959, *S*heremetye*vo* International is located in the city of Khimki, but was named after the nearby village of Sheremetyevsky.", "imageCredit": "Jaime-Silva", "imageCreditLink": "https://www.flickr.com/photos/20792787@N00/" },
"svq": { "id": "svq", "name": "Aeropuerto de Sevilla", "nameEnglish": "Seville Airport", "city": "Seville", "country": "Spain", "description": "Seville Airport, also known as San Pablo Airport, began commercial flights in 1919. Its airport code comes from the city of *S*e*v*ille, plus a *Q* as a filler letter.", "imageCredit": "Luis Jou Garca", "imageCreditLink": "https://www.flickr.com/photos/luisjoujr/" },
