"sxb": { "id": "sxb", "name": "Aroport International de Strasbourg", "nameEnglish": "Strasbourg International Airport", "city": "Strasbourg", "city2": "Entzheim", "country": "France", "description": "Many airports use the letter X to fill in when the desired letters are already taken. Actually located in the village of Entzheim, this airport gets its code from the town of *S*tras*b*ourg plus that helpful *X*.", "imageCredit": "Gogo6969", "imageCreditLink": "https://www.flickr.com/photos/wgaebler/" },
"sxf": { "id": "sxf", "name": "Flughafen Berlin-Schnefeld", "nameEnglish": "Berlin Schnefeld Airport", "city": "Berlin", "city2": "Schnefeld", "city3": "Schonefeld", "country": "Germany", "description": "Many airports use the letter X to fill in when the desired letters are already taken. This Berlin airport get its code from its home in the town of *S*chne*f*eld plus that helpful *X*.", "imageCredit": "Aero Icarus", "imageCreditLink": "https://www.flickr.com/photos/aero_icarus/" },
