"sxr": { "id": "sxr", "name": "Srinagar International Airport", "city": "Srinagar", "state": "Jammu and Kashmir", "stateShort": "JK", "country": "India", "description": "Srinagar International is located in the city of *S*rinaga*r* in Jammu and Kashmir and is known for serving pilgrimage charters to Jeddah. Its airport code is completed with an *X*, often used as a filler letter.", "imageCredit": "Kiran SRK", "imageCreditLink": "https://www.flickr.com/photos/srkkiran/" },
"syd": { "id": "syd", "name": "Sydney (Kingsford Smith) Airport", "city": "Sydney", "state": "New South Wales", "stateShort": "NSW", "country": "Australia", "description": "Kingsford Smith Airport is named in honor of Sir Charles Kingsford Smith, Australian aviator. Its airport code honors its home in *Syd*ney.", "imageCredit": "Bobby Das", "imageCreditLink": "https://www.flickr.com/photos/bobbydas/" },
"syr": { "id": "syr", "name": "Syracuse Hancock International Airport", "city": "Syracuse", "state": "New York", "stateShort": "NY", "country": "USA", "description": "Syracuse Hancock International opened for passenger travel in 1949 and honors Clarence E. Hancock, former New York congressman. Its airport codes honors the city of *Syr*acuse.", "imageCredit": "New York National Guard", "imageCreditLink": "https://www.flickr.com/photos/nyng/" },
"syx": { "id": "syx", "name": "Sanya Phoenix International Airport", "city": "Sanya", "state": "Hainan", "country": "China", "description": "Opened in 1994, Sanya Phoenix International gets its name and airport code from its home in the city of *S*an*y*a and nearby Phoeni*x* Mountain.", "imageCredit": "depps", "imageCreditLink": "https://www.flickr.com/photos/depps_next/" },
"syz": { "id": "syz", "name": "Shiraz International Airport", "city": "Shiraz", "country": "Iran", "description": "Renovated in 2005, Shiraz International gets its three-letter code from its home in the city of *S*hira*z*. It is completed with a *Y*, often used as a filler letter.", "imageCredit": "Mahyar Ebrahimi", "imageCreditLink": "http://commons.wikimedia.org/wiki/File:Shiraz_Panoramic_view.jpg" },
"szb": { "id": "szb", "name": "Lapangan Terbang Sultan Abdul Aziz Shah", "nameEnglish": "Sultan Abdul Aziz Shah Airport", "city": "Subang", "city2": "Kuala Lumpur", "state": "Selangor", "country": "Malaysia", "description": "Sultan Abdul Aziz Shah Airport gave its airport code KUL to Kuala Lumpur International in 1998. Its new code comes from its current and former names: *S*ultan Abdul A*z*iz Shah / Su*b*ang Airport.", "imageCredit": "SunCon Photos", "imageCreditLink": "https://www.flickr.com/photos/suncon/" },
"szg": { "id": "szg", "name": "Salzburg Airport W. A. Mozart", "city": "Salzburg", "country": "Austria", "description": "Austrias second largest airport is named after classical composer Wolfgang Amadeus Mozart, but its airport code honors its home in *S*al*z*bur*g*.", "imageCredit": "flightlog", "imageCreditLink": "https://www.flickr.com/photos/flightlog/" },
