"tbz": { "id": "tbz", "name": "Tabriz International Airport", "city": "Tabriz", "country": "Iran", "description": "Serving both military and civilian traffic, Tabriz International gets its three-letter code from its home in *T*a*b*ri*z*.", "imageCredit": "Pegah Naeimi", "imageCreditLink": "https://www.flickr.com/photos/pegahnaeimi/" },
