"yip": { "id": "yip", "name": "Willow Run Airport", "city": "Ypsilanti", "state": "Michigan", "stateShort": "MI", "country": "USA", "description": "Until 1958, Willow Run was Detroits primary airport. Its three-letter code represents the nearby town of *Y*psilanti and its nicknames pronunciation of *Ip*-see.", "imageCredit": "ellenm1", "imageCreditLink": "https://www.flickr.com/photos/ellenm1/" },
